.slide20 {

  @include tablet{
    display: none;
  }
    .default-slide__box{
        grid-template-columns: 1fr 1.4fr;
      align-items: center;
      justify-items: center;

    }

    .default-slide__info{
        max-width: 460px;

      
    }

    .default-slide__picture {
      display: flex;
      align-items: center;
      justify-content: center;
        margin-bottom: -6%;
      
  
      img,
      picture {
        width: auto;
        height: 70vh;
      
  
        @include laptop {
          height: 61vh;
          width: auto;
        }
  
        @include small-tablet {
          height: 60vh;
        }
      }
  
      @include tablet {
        justify-content: center;
      }
    }
  }
  